<template>
  <div class="withdraw-record" style="padding-top: 44px;">
    <backheader title="提现记录" />
    <scroll ref="scroll" @scrollToEnd="scrollToEnd" :is-finished="finished">
      <div v-for="item in list" :key="item.id" class="item">
        <h6>{{ item.create_time }} <img :src="icons[item.status]" alt=""></h6>
        <p>申请提现：¥{{ item.apply_cash }}</p>
        <p v-if="item.commission_cash">服务费：¥{{ item.commission_cash }}</p>
        <p>收款支付宝：{{ item.alipay_account }}</p>
        <p v-if="item.trade_no">转账单号：{{ item.trade_no }}</p>
      </div>
    </scroll>
  </div>
</template>

<script>
import Backheader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

const PAGE_SIZE = 10

export default {
  name: 'withdraw-record',
  components: {
    Backheader,
    Scroll
  },
  data() {
    return {
      list: [],
      icons: [require('./dengdaichuli@2x.png'), require('./yiwancheng@2x.png'), require('./yibohui@2x.png'), require('./yichang@2x.png')],
      page: 1,
      loading: false,
      finished: true
    }
  },
  created() {
    this.getList()
  },
  methods: {
    scrollToEnd() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.getList()
    },
    getList() {
      post('/shop/shop/withdrawLog', { page: this.page, size: PAGE_SIZE })
        .then(({ data: { data } }) => {
          this.list = this.list.concat(data)
          this.finished = data.length < PAGE_SIZE
          this.loading = false
        })
    }
  }
}
</script>

